import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {ConfigProvider} from "../../providers/config/config";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";


@IonicPage()
@Component({
  selector: 'page-fund-transer-other-bank02',
  templateUrl: 'fund-transer-other-bank02.html',
})
export class FundTranserOtherBank02Page {

  otherBankTransferError02;
  srcAccDetails;
  transferTypes;
  receiverTypes: any = [];
  fundTransferType;
  ftOBDestType;
  receiverType;
  accountNo;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public iTransferService: ITransferServiceProvider,
              public tokenService: TokenServiceProvider,
              public config: ConfigProvider,
              public ftOtherBankService: FundTransferOtherBankProvider
              ) {

    this.accountNo = this.navParams.get("accountNo");
    this.srcAccDetails = JSON.parse(this.navParams.get("accountDetails"));
    this.transferTypes = JSON.parse(this.navParams.get("transferTypes"));
    this.ftOBDestType = JSON.parse(this.navParams.get("ftOBDestType"));
  }

  onSelectChange(selectedValue) {
    this.receiverTypes=[];
    this.receiverTypes= this.ftOBDestType[selectedValue]
  }

  iTransferOtherBank02Action = function () {
    this.otherBankTransferError02 = "";
    let flag = true;
    if (this.fundTransferType == "" || typeof(this.fundTransferType) == "undefined") {
      this.otherBankTransferError02 = "Please Select transfer type.";
      flag = false;
      return;
    }

    if (this.receiverType == "" || typeof(this.receiverType) == "undefined") {
      this.otherBankTransferError02 = "Please Select Receiver type.";
      flag = false;
      return;
    }


    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {
    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encSrcAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encTransferType = this.cryptoUtil.getEncryptVal(this.fundTransferType);
      let encRecType = this.cryptoUtil.getEncryptVal(this.receiverType);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.ftOtherBankService.fetchDestinationAccountList(unMaskToken, appData.userId, appData.appId, encSrcAccNo, encRecType, encTransferType)
        .then(data => {
          this.destinationAccountFetchComplete(data);
        });
    }
  };

  destinationAccountFetchComplete = function (data) {

    if (data.statusCode == 1) {
      this.accountNo = this.navParams.get("accountNo");
      this.srcAccDetails = JSON.parse(this.navParams.get("accountDetails"));
      this.transferTypes = JSON.parse(this.navParams.get("transferTypes"));

      let destAccount=data.object.obFtBeneficiaries;
      if(destAccount==null){
        this.otherBankTransferError02="You have no beneficiary subscription for this type of transfer.";
        return;
      }

      this.navCtrl.push('FundTransferOtherBankRecListPage', {
        'accountNo': this.accountNo,
        'srcAccDetails': JSON.stringify(data.object.accInfoBean),
        'purposeList': JSON.stringify(data.object.ftPurposeList),
        'destAccList': JSON.stringify(data.object.obFtBeneficiaries),
        'fundTransferType' : this.fundTransferType,
        'receiverType' : this.receiverType

      });
    }

    else if (data.statusCode == 0) {
      this.otherBankTransferError02 = data.note;
    }

    else {
      this.otherBankTransferError02 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ITransfer02Page');
  }

}




